.MyOrder {
  width: 360px;
  padding: 24px;
  box-sizing: border-box;
  position: absolute;
  right: 0;
  background-color: #fff;
  top: 120px;
  z-index: 1;
  bottom: auto;
  border-radius: 6px;
  border: 1px solid #c7c7c7;
  animation: swipeLeft 0.26s ease;
}
@keyframes swipeLeft {
  0% {
    width: 0;
  }
  100% {
    width: 360px;
  }
}
.title-container {
  display: flex;
}
.title-container img {
  transform: rotate(180deg);
  margin-right: 14px;
}
.title {
  font-size: 18px;
  font-weight: bold;
}
.order {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 16px;
  align-items: center;
  background-color: #f7f7f7;
  margin-bottom: 24px;
  border-radius: 8px;
  padding: 0 24px;
}
.order p:nth-child(1) {
  display: flex;
  flex-direction: column;
}
.order p span:nth-child(1) {
  font-size: 16px;
  font-weight: bold;
}
.order p:nth-child(2) {
  text-align: end;
  font-weight: bold;
}

.primary-button {
  background-color: #df2020;
  border-radius: 8px;
  border: none;
  color: #fff;
  width: 90%;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  height: 40px;
  margin: 1rem;
}
@media (max-width: 640px) {
  .MyOrder {
    width: 100%;
  }
}
.empty-cart-notififaction {
  font-family: "AdineuePRO", Helvetica, Arial, sans-serif;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #000;
}
